<template>
  <div class="order">
    <van-nav-bar
      title="我的订单"
      left-text="返回"
      left-arrow
      fixed
      @click-left="back"
    />
    <Bg>
      <div class="tab-box">
        <van-tabs
          v-model="tabIndex"
          color="#eb2403"
          line-height="2px"
          title-active-color="#eb2403"
          animated
          duration="0.35"
          @change="changeOrderStatus"
          sticky
          offset-top="46px"
        >
        <!-- 粘性定位  -->
          <van-tab
            :title="item.title"
            v-for="(item, index) in tabData"
            :key="index"
          >
            <div class="tab-content">
                <div v-if="orderData.length > 0">
              <div class="order-content" v-for="(item,index) in orderData" :key="index">
                <div class="o-title">
                  <div class="o-num">{{item.orderId}}</div>
                  <div class="o-status">
                    <div class="o-finished" v-if="item.status === 2">
                      <div>已完成</div>
                      <div class="o-remove" @click="removeOrder(item.orderId, index)">
                        <van-icon name="delete-o" />
                      </div>
                    </div>
                    <div v-else @click="receive(item)">确认收货</div>
                  </div>
                </div>
                <div>
                  <div class="o-pro-item" v-for="(v ,i) in item.data" :key="i">
                    <ProList class="not-padding" :isHasCheckbox="false" :proData="v">
                      <div class="o-count">x{{v.count}}</div>
                    </ProList>
                  </div>
                </div>
                <div class="o-bottom">
                  <div class="empty-round">
                    <div class="round-box left-r"></div>
                    <div class="round-box right-r"></div>
                  </div>
                  <div class="o-date">
                      {{ item.date | formatDate("yyyy-MM-dd hh:mm:ss") }}
                  </div>
                  <div class="o-count-price">
                    <div class="o-c1">共计 {{item.total}} 件</div>
                    <div class="o-c2">总价: <span>&yen;{{item.money}}</span></div>
                  </div>
                </div>
                </div>
              </div>
               <div v-else>
                <van-empty description="没有订单数据！" />
              </div>
            </div>
          </van-tab>
        </van-tabs>
      </div>
    </Bg>
  </div>
</template>

<script>
import "../assets/less/order.less";
import Bg from "../components/Bg.vue";
import ProList from "../components/ProList.vue";
export default {
  name: "Order",
  components: {
    Bg,
    ProList
  },
  data() {
    return {
      tabIndex: 0,
      tabData: [
        {
          title: "全部",
        },
        {
          title: "进行中",
        },
        {
          title: "已完成",
        },
      ],
        //   订单数据
        orderData: [],
    };
  },
  created(){
      this.getUserOrderData(this.tabIndex)
  },
  methods: {
    //返回上一级
    back() {
      this.$router.go(-1);
    },
    // 获取用户订单数据
    getUserOrderData(status){
        // status: 订单状态
        //其中 status 值为 0, 1, 2 ==> 0: 全部，1: 进行中，2: 已完成
        //获取token
        let tokenString = this.$cookies.get('TOKEN');

        this.axios({
          method: "GET",
          url: "/findOrder",
          params: {
            tokenString,
            status
          }
        })
          .then((res) => {
            
            

            if (res.data.code == 700) {
              //用户未登录, 拦截操作, 通知用户去登录
              this.$toast('请先登录');
              setTimeout(() => {
                this.$router.push({name: 'Login'});
              }, 2000)
              return;
            }

            if(res.data.code === 70000){
                // 获取订单编号
                let orderNos = [];
                res.data.result.forEach(v =>{
                    v.small_img = v.smallImg;
                    delete v.smallImg;
                    if(orderNos.indexOf(v.oid) === -1){
                        orderNos.push(v.oid);
                    }
                })
                
                // 按照订单编号分类
                    // [
                    //   {
                    //     orderId: 订单编号
                    //     date: 日期,
                    //     total: 数量,
                    //     money: 总金额,
                    //     status: 订单状态
                    //     商品数据
                    //     data: [
                    //       {},
                    //       {},
                    //       ...
                    //     ]
                    //   }
                    // ]
                    let orderData = [];
                    orderNos.forEach(v1 =>{
                        let order = {
                            orderId: v1,
                            date: "",
                            total: 0,
                            money: 0,
                            status: -1,
                            data: []
                        };
                        // 查找订单
                        res.data.result.forEach(v2 =>{
                            if(v1 === v2.oid){
                                if(!order.date){
                                    order.date = v2.createdAt;
                                }
                                if(order.status === -1){
                                    order.status = v2.status;
                                }
                                order.total += v2.count;
                                order.money += v2.count * v2.price;
                                order.data.push(v2);
                            }
                        })
                        orderData.push(order);
                    })
                    

                    this.orderData = orderData;
            }
          })
          .catch((err) => {
            
          });
    },
    // 切换订单状态
    changeOrderStatus(){
        
        this.getUserOrderData(this.tabIndex)
    },
    // 确认收货
    receive(item){
        this.$dialog
        .confirm({
          title: "提示",
          message: "是否确认收货？",
        })
        .then(() => {
          //发起确认收货请求
          let tokenString = this.$cookies.get("TOKEN");

          this.axios({
            method: "POST",
            url: "/receive",
            data: {
              tokenString,
              oid: item.orderId,
            },
          })
          .then((res) => {
              

              if (res.data.code == 700) {
                //用户未登录, 拦截操作, 通知用户去登录
                this.$toast("请先登录");
                setTimeout(() => {
                  this.$router.push({ name: "Login" });
                }, 2000);

                return;
              }

              this.$toast(res.data.msg);
              if (res.data.code === 80000) {
                  item.status = 2;
                  //如果是在进行中订单确认收货，需要移除订单数据
                  if(this.tabIndex ===1){
                      for(let i = 0; i < this.orderData.length; i++){
                          if(item.oid === this.orderData.orderId){
                              this.orderData.splice(i,1);
                              return;
                          }
                      }
                  }
              }
            })
            .catch((err) => {
              
            });
        })
    },
    // 删除
    removeOrder(oid, index){
        this.$dialog
        .confirm({
          title: "提示",
          message: "是否确认删除？",
        })
        .then(() => {
          //发起确认收货请求
          let tokenString = this.$cookies.get("TOKEN");

          this.axios({
            method: "POST",
            url: "/removeOrder",
            data: {
              tokenString,
              oid
            },
          })
          .then((res) => {
              

              if (res.data.code == 700) {
                //用户未登录, 拦截操作, 通知用户去登录
                this.$toast("请先登录");
                setTimeout(() => {
                  this.$router.push({ name: "Login" });
                }, 2000);

                return;
              }

              this.$toast(res.data.msg);
              if (res.data.code === 90000) {
                //   删除数据
                  this.orderData.splice(index, 1);
              }
            })
            .catch((err) => {
              
            });
        })
    }
  },
};
</script>


<style lang="less" scoped>
.order {
  /deep/ .van-nav-bar .van-icon {
    color: #eb2403;
  }

  /deep/ .van-nav-bar__text {
    color: #eb2403;
  }
}
</style>